import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { AlertController } from 'ionic-angular';
import { AuthService } from '../../providers/auth-service';
import { AngularFire, FirebaseListObservable } from 'angularfire2';

@IonicPage()
@Component({
  selector: 'page-vocab',
  templateUrl: 'vocab.html',
})
export class Vocab {
  vocab: FirebaseListObservable<any[]>;
  arr2D: string[][];
  constructor(public navCtrl: NavController, public navParams: NavParams, public angularFire: AngularFire) {
    this.vocab = angularFire.database.list('/testUpdate');
  }

  ionViewDidLoad() {  }

  getVocab() {
    this.vocab.subscribe(queriedItems => {
      console.log(queriedItems);
    });
  }

  addVocab() {
    this.vocab.push({
          username: "username",
          email: "mail",         
          status: "No Status Now",
          number: 1
    });
  }
}



